@import '../../styles//variables.scss';

.days{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 20px;
    width: 100%;
    background: var(#{--components-background-default});
    box-shadow: $box-shadow;
    border-radius: 0 0 $border-radius $border-radius;
}

.card{
    display: flex;
    flex-direction: column;
    background: var(#{--card-background-default});
    border-radius: 10px;
    width: 149px;
    transition: 0.3s;
    cursor: pointer;
    padding: 10px;
    .day{
        margin-bottom: 7px;
        font-size: 18px;
        color: var(#{--text-color-default});
    }

    .day__info{
        font-size: 14px;
        margin-bottom: 12px;
        color: $base-color;

    }
    .img{
        margin-bottom: 12px;

    }
    .temp__night{
        font-size: 13px;
        color: $base-color;
        margin-bottom: 6px;
    }
    .temp__day{
        font-size: 18px;
        color: var(#{--text-color-default});
    }

    .info{
        font-size: 13px;
        color: $base-color;
    }
    
    &:hover{
        box-shadow: var(#{--card-background-default});
    }
    
}

.tabs{
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    margin-bottom: 5px;
}

.cancel,
.tab{
    box-shadow: 2px 5px 25px -3px rgba(180,180,180,0.25);
    border-radius: 5px;
    padding: 9px 20px;
    background-color: var(#{--components-background-default});
    margin-right: 15px;
    color: var(#{--text-background-default});
    font-size: 18px ;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 15px;
    cursor: pointer;
}

.cancel{
    margin-right: 0;
}

.tabs__wrapper{
    display: flex;
}
.tab{
    &.active{
        background: $blue-color;
        color: #fff;
    }
}