@import '../../../styles/variables.scss';
.header{
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    margin-bottom: 30px;
}

.wrapper{
    display: flex;
    align-items: center;
}

.change__them,
.logo {
    margin-right: 20px;
    cursor: pointer;
}

.logo{
    margin-right: 20px;
    svg{
        width: 65px;
        height: 65px;
    }
}

.title{
    text-transform: uppercase;
    font-size: 25px;
    font-weight: bold;
    color: $blue-color;
}