@import '../../styles/variables.scss';


.this__day{
    padding: 20px;
    max-width: 400px;
    width: 100%;
    background: var(#{ --components-background-default});
    box-shadow: $box-shadow;
    border-radius: $border-radius;
}

.top__block{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 30px;
    svg{
        width: 119px;
        height: 119px;
    }
    .this__temp{
        font-size: 96px;
        font-weight:600;
        color: $blue-color;
    }
    .this__day_name{
        font-size: 40px;
        color: var(#{--text-background-default});
    }
}

.bottom__block{
    .this__time,
    .this__city{
        color: $base-color;
    }
}
